import { Divider, Fade, IconButton, CircularProgress, Popover, makeStyles, Paper, Typography, createTheme } from '@material-ui/core';
import Modal from '@material-ui/core/Modal';
import CloseIcon from '@material-ui/icons/Close';
import { useState } from 'react';
import { useEffect } from 'react';
import Box from '../../assets/images/box.png';
import correios from '../../services/correios';
import { BoxModal, CityTextItem, CodRastreioModal, CodTextModal, DateTextItem, DescriptionTextItem, HistoricItem, ModalContainer, RowBetween, RowEnd, ScrollContainer, TitleModal } from '../../styles';
import MapGL from "react-map-gl";
import Geocode from "react-geocode";

function ModalHistoric({ openHistoric, handleClose, product, historic }) {
  const [openPopover, setOpenPopover] = useState(false)
  const [historico, setHistorico] = useState([])
  const [viewport, setViewport] = useState({
    latitude: -12.676084,
    longitude: -51.005864,
    zoom: 1
  });
  const [anchorEl, setAnchorEl] = useState(null);

  const theme = createTheme({
    breakpoints: {
      values: {
        md: 426,
      },
    },
  });

  const useStyles = makeStyles({
    paperModal: {
      [theme.breakpoints.up('md')]: {
        width: '30rem',
        height: '30rem',
        backgroundColor: '#303030',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        borderRadius: '1rem',

        '&:focus': {
          outline: 'none',
          border: 'none',
        },
      },

      width: '80rem',
      height: '80rem',
      backgroundColor: '#303030',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      borderRadius: '2rem',

      '&:focus': {
        outline: 'none',
        border: 'none',
      },
    },
    popover: {
      width: '40rem',
      height: '30rem',
    },
    exitButton: {
      [theme.breakpoints.up('md')]: {
        position: 'absolute',
        top: '0.5rem',
        right: '0.5rem',
      },

      position: 'absolute',
      top: '0.5rem',
      right: '0.5rem',
    },
    exitIcon: {
      [theme.breakpoints.up('md')]: {
        fontSize: '1.2rem',
      },

      fontSize: '4rem',
    }
  });

  const classes = useStyles();

  function handleOpenPopover(e, item) {
    setAnchorEl(e.currentTarget)
    Geocode.setApiKey("AIzaSyBejIiHoswQfyYSLZaGYoYIk7MKZMNDaU0");
    Geocode.setLanguage("pt-br");
    Geocode.setRegion("br");
    Geocode.fromAddress(item.cidade).then(
      (response) => {
        setViewport({
          latitude: response.results[0].geometry.location.lat,
          longitude: response.results[0].geometry.location.long,
          zoom: 2
        })
        setTimeout(2000, () => {console.log(viewport)})
      },
      (error) => {
        console.error(error);
      }
    );
    setOpenPopover(true)
  }

  function handleClosePopover() {
    setOpenPopover(false)
    setViewport(null)
  }

  useEffect(() => {
    correios.get('', {
      params: {
        codigo: product.codRastreio
      }
    }).then(response => {
      setHistorico(response.data)
    })
  }, [product])

  return (
    <Modal
      open={openHistoric}
      onClose={handleClose}
      style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center' }}
      closeAfterTransition
      BackdropProps={{
        timeout: 500,
      }}
    >
      <Fade in={openHistoric} timeout={400}>
        <Paper style={{ position: 'relative', padding: '1rem' }} className={classes.paperModal}>
          <IconButton className={classes.exitButton} onClick={handleClose}>
            <CloseIcon className={classes.exitIcon} style={{ color: '#FFF' }} />
          </IconButton>
          <ModalContainer style={{ color: 'white' }}>
            <RowBetween>
              <BoxModal src={Box} alt="Box" />
              <TitleModal>
                {product.product}
              </TitleModal>
            </RowBetween>
            <Divider style={{ backgroundColor: 'white', opacity: '0.3' }} />
            <RowEnd style={{ marginTop: '0.4rem' }}>
              <CodRastreioModal>
                <CodTextModal>
                  Cód. Rastreio
                </CodTextModal>
                {product.codRastreio}
              </CodRastreioModal>
            </RowEnd>
            <ScrollContainer>
              {historico ? historico.map(item => {
                return (
                  <>
                    <HistoricItem key={item.dataHora} onClick={(e) => {handleOpenPopover(e, item)}}>
                      <RowBetween>
                        <CityTextItem>
                          {item.cidade}{item.uf ? ` - ${item.uf}` : ''}
                        </CityTextItem>
                        <DateTextItem>
                          {item.dataHora}
                        </DateTextItem>
                      </RowBetween>
                      <DescriptionTextItem>
                        {item.descricao.includes('Objeto em trânsito') ? `Objeto em trânsito para ${item.destino.cidade}${item.destino.uf ? ' - ' + item.destino.uf : ''}` : item.descricao }
                      </DescriptionTextItem>
                    </HistoricItem>
                    <Popover
                      open={openPopover}
                      onClose={handleClosePopover}
                      anchorEl={anchorEl}
                      className={classes.popover}
                      anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'center',
                      }}
                      transformOrigin={{
                        vertical: 'top',
                        horizontal: 'center',
                      }}
                    >
                      <MapGL
                        attributionControl={false}
                        width="20rem"
                        height="15rem"
                        {...viewport}
                        mapStyle="mapbox://styles/speycer/ckrfn9klb2tj517moc3d7u6r3"
                        mapboxApiAccessToken='pk.eyJ1Ijoic3BleWNlciIsImEiOiJja3JmbHYzeDY1eTNqMm5sMzZ6b3F6YjNxIn0.GUFClPNRuUu50n3aP8Wp5g'
                        onViewportChange={viewport => setViewport(viewport)}
                      />
                    </Popover>
                  </>
                )
              }) : <CircularProgress style={{ color: '#FFF' }} />}
            </ScrollContainer>
          </ModalContainer>
        </Paper>
      </Fade>
    </Modal>
  );
}

export default ModalHistoric;