import { Divider, makeStyles, Typography } from "@material-ui/core";
import { BoxCard, Card, CodRastreioCard, CodTextCard, Column, Row, TitleCard } from "../../styles";

import Box from '../../assets/images/box.png';
import { useState } from "react";
import MapGL from "react-map-gl";
import Geocode from "react-geocode";

import './styles.css';
import { useEffect } from "react";

function CardItem({ product, handleOpen }) {
  const [viewport, setViewport] = useState({
    latitude: -12.676084,
    longitude: -51.005864,
    zoom: 1
  });

  const useStyles = makeStyles({
    input: {
      '& + &': {
        marginLeft: '1rem',
      },
      '& label.Mui-focused': {
        color: '#303030',
        fontFamily: 'Ubuntu',
      },

      '.MuiOutlinedInput-input': {
        fontFamily: 'Ubuntu',
      },

      '& .MuiOutlinedInput-root': {
        '& fieldset': {
          borderColor: '#303030',
          fontFamily: 'Ubuntu',
        },
        '&:hover fieldset': {
          borderColor: '#464646',
        },
        '&.Mui-focused fieldset': {
          borderColor: '#303030',
        },
      },
    },
    button: {
      width: '12.5rem',
      backgroundColor: '#303030',
      color: '#FFF',
      font: '400 0.85rem Ubuntu',
      padding: '1rem 4rem',
      borderRadius: '0.438rem',
      marginLeft: '2.125rem',
      transition: '400ms',

      '&:hover': {
          backgroundColor: '#464646',
      },
    }
  });

  const classes = useStyles();

  useEffect(() => {
    Geocode.setApiKey("AIzaSyBejIiHoswQfyYSLZaGYoYIk7MKZMNDaU0");
    Geocode.setLanguage("pt-br");
    Geocode.setRegion("br");
    Geocode.fromAddress(product.cepDestino).then(
      (response) => {
        // console.log(response)
        // setViewport({
        //   latitude: response.results[0].geometry.location.lat,
        //   longitude: response.results[0].geometry.location.long,
        //   zoom: 2
        // })
      },
      (error) => {
        console.error(error);
      }
    );
  }, [product])

  return (
    <Card onClick={() => handleOpen(product)}>
      <BoxCard src={Box} alt="Box" />
      <Column>
        <TitleCard>
          {product.product}
        </TitleCard>
        <Divider style={{ margin: '0.1rem 0' }} />
        <Row style={{ marginTop: '0.25rem' }}>
          <CodTextCard>
            Cód. Rastreio
          </CodTextCard>
          <CodRastreioCard>
            {product.codRastreio}
          </CodRastreioCard>
        </Row>
      </Column>
      <MapGL
        attributionControl={false}
        width="100%"
        height="44%"
        {...viewport}
        mapStyle="mapbox://styles/speycer/ckrfn9klb2tj517moc3d7u6r3"
        mapboxApiAccessToken='pk.eyJ1Ijoic3BleWNlciIsImEiOiJja3JmbHYzeDY1eTNqMm5sMzZ6b3F6YjNxIn0.GUFClPNRuUu50n3aP8Wp5g'
        onViewportChange={newViewport => setViewport(newViewport)}
      />
    </Card>
  )
}

export default CardItem;